/* App.css */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Segoe UI', sans-serif;
  background: #0e0e0e;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  background: rgba(30, 30, 30, 0.9);
  padding: 30px 40px;
  border-radius: 20px;
  box-shadow: 0 0 15px #0ff, 0 0 30px #f0f;
  border: 1px solid #2a2a2a;
  width: 320px;
  text-align: center;
  position: relative;
}

.App h2 {
  color: #fff;
  font-size: 24px;
  margin-bottom: 20px;
}

input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 12px 16px;
  margin: 10px 0;
  background: #1e1e1e;
  border: 2px solid #333;
  border-radius: 25px;
  color: #fff;
  outline: none;
  font-size: 14px;
  transition: border 0.3s ease, box-shadow 0.3s ease;
}

input[type="text"]:focus,
input[type="password"]:focus {
  border-color: #0ff;
  box-shadow: 0 0 8px #0ff;
}

button {
  margin-top: 10px;
  background: #00ffff;
  color: #000;
  border: none;
  padding: 12px 20px;
  border-radius: 25px;
  font-weight: bold;
  cursor: pointer;
  width: 100%;
  transition: background 0.3s ease;
}

button:hover {
  background: #00cccc;
}

.error {
  color: #ff4d4d;
  margin-top: 10px;
  font-size: 13px;
}

.App .links {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  font-size: 13px;
}

.App .links a {
  color: #ff4dff;
  text-decoration: none;
}

.App .links a:hover {
  text-decoration: underline;
}