.landing-container {
  height: 100vh;
  background: linear-gradient(180deg, #0e0e0e 0%, #1a0033 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-align: center;
  padding: 20px;
  position: relative;
  font-family: 'Segoe UI', sans-serif;
}

.landing-title {
  font-size: 36px;
  margin-bottom: 12px;
  line-height: 1.4;
}

.highlight {
  color: #b266ff;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(178, 102, 255, 0.4);
}

.speech-bubble {
  background: rgba(255, 255, 255, 0.1);
  padding: 12px 22px;
  border-radius: 25px;
  margin: 20px 0;
  font-size: 15px;
  box-shadow: 0 0 15px rgba(200, 200, 255, 0.25);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.bot-image {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  object-fit: cover;
  margin: 20px 0;
  animation: float 3s ease-in-out infinite;
  box-shadow: 0 0 15px rgba(178, 102, 255, 0.3);
  border: 2px solid #b266ff;
}

.get-started-btn {
  background: #a166ff;
  border: none;
  padding: 12px 30px;
  border-radius: 30px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 0 12px #a166ff;
  transition: background 0.3s, transform 0.2s;
}

.get-started-btn:hover {
  background: #8033cc;
  transform: translateY(-2px);
}

@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
  100% { transform: translateY(0); }
}